import React from 'react';
import Header from '../../component/header/header';
import Footer from '../../component/footer/footer';
import { Button, ButtonArea } from '../../../src';

const ButtonPage = () => (
  <div className="page">
    <Header title="Button" desc="" />
    <div className="page__content page__content_spacing">
      <ButtonArea>
        <Button></Button>
        <Button selected></Button>
        <Button disabled></Button>
      </ButtonArea>

      <ButtonArea>
        <Button type="warn"></Button>
        <Button type="warn" selected></Button>
        <Button type="warn" disabled></Button>
      </ButtonArea>

      <ButtonArea>
        <Button type="default"></Button>
        <Button type="default" selected></Button>
        <Button type="default" disabled></Button>
      </ButtonArea>

      <ButtonArea>
        <Button type="default" size="small"></Button>
        <Button type="default" size="small" selected></Button>
        <Button type="default" size="small" disabled></Button>
      </ButtonArea>

      <ButtonArea direction="horizontal">
        <Button></Button>
        <Button></Button>
      </ButtonArea>

      <ButtonArea direction="horizontal">
        <Button></Button>
        <Button selected style={{ maxWidth: 70 }}></Button>
      </ButtonArea>
    </div>
    <Footer title="YSUI" />
  </div>
);

export default ButtonPage;



